@import "../../App.scss";

/************************/

.header-product {
  background-color: var(--color-2);
  display: inline-flex;
  justify-content: space-between;
  align-items: center;
  padding: 95px 40px 15px 40px;
  width: 100%;
  max-width: 100vw;

  @include breakpoints(sm) {
    background: #545776;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    padding: 95px 13px 15px 13px;
  }

  h4,
  h1,
  svg {
    color: #fff;
  }

  svg {
    font-size: 3.5rem;
    padding: 0;

    @include breakpoints(sm) {
      font-size: 2.5rem;
      position: absolute;
      right: 0;
    }
  }
}

/************************/

.location-product {
  background-color: var(--color-4);
  display: inline-flex;
  justify-content: space-between;
  align-items: center;
  color: var(--color-2);
  position: relative;
  z-index: -1;

  h4 {
    @include breakpoints(sm) {
      font-size: 14px;
    }
  }

  .location-content {
    display: flex;
    width: 50%;
    align-items: flex-start;
    position: relative;

    svg {
      font-size: 1.3rem;
    }

    :first-child {
      padding-right: 3px;
    }

    .distance-text {
      @include breakpoints(sm) {
        display: none;
      }
    }
  }
}

/************************/

.description-product {
  h1 {
    padding: 40px 0px;

    @include breakpoints(sm) {
      padding: 20px 0px;
    }
  }

  p {
    padding: 10px 0px;
    line-height: 20px;
  }
}

/************************/

.characteristics-product {
  .characteristics-grid-container {
    display: grid;
    display: -ms-grid;
    gap: 40px;
    grid-template-columns: repeat(4, minmax(20%, 1fr));
    -ms-grid-columns: repeat(4, minmax(20%, 1fr));
    font-weight: bold;
    color: var(--gris-plomo);

    @include breakpoints(md) {
      grid-template-columns: repeat(2, minmax(50%, 1fr));
      -ms-grid-columns: repeat(2, minmax(50%, 1fr));
    }

    @include breakpoints(sm) {
      grid-template-columns: repeat(2, minmax(45%, 0fr));
      -ms-grid-columns: repeat(2, minmax(45%, 0fr));
      gap: 20px;
    }

    svg,
    img {
      color: var(--color-principal);
      margin-right: 5px;
    }
  }
}

/************************/

.politics-product {
  .politics-grid-container {
    display: grid;
    grid-template-columns: repeat(3, minmax(25%, 1fr));
    -ms-grid-columns: repeat(3, minmax(25%, 1fr));

    @include breakpoints(md) {
      grid-template-columns: repeat(2, minmax(50%, 1fr));
      -ms-grid-columns: repeat(2, minmax(50%, 1fr));
    }

    @include breakpoints(sm) {
      grid-template-columns: repeat(1, minmax(100%, 1fr));
      -ms-grid-columns: repeat(1, minmax(100%, 1fr));
    }

    h3 {
      color: var(--color-3);
      margin-bottom: 40px;

      @include breakpoints(sm) {
        margin-bottom: 30px;
      }
    }

    .politics-items {
      margin-right: 70px;
      margin-bottom: 50px;

      div {
        margin-top: 20px;
      }
    }
  }
}

/************************/

.charactetistics-title,
.politics-title {
  padding: 20px 0px 40px 0px;
  gap: 10px;

  h1 {
    padding-bottom: 20px;
  }
}

// .header-product,
.location-product,
.description-product,
.characteristics-product,
.politics-product {
  padding: 15px 40px 15px 40px;
  width: 100%;
  max-width: 100vw;

  @include breakpoints(sm) {
    padding: 15px 13px;
  }
}

.header-product,
.description-product,
.characteristics-product,
.politics-product {
  h1 {
    @include breakpoints(sm) {
      font-size: 24px;
    }
  }
}

.description-product,
.characteristics-product,
.politics-product {
  h1 {
    color: var(--color-2);
  }
}

.characteristics-product,
.politics-product {
  hr {
    position: absolute;
    right: 10px;
    left: 0px;
    background-color: var(--color-principal);
    border: 0.01px solid var(--color-principal);
  }
}

.characteristics-grid-container div,
.politics-items div,
.description-product p {
  font-size: 16px;
  color: var(--gris-plomo);
  font-weight: 700;

  @include breakpoints(sm) {
    font-size: 14px;
  }
}
