.react-datepicker__close-icon{

}

.react-datepicker__close-icon::after {
    
    background-color: #ffffff;
    color: #545776;
    
    font-size: 12px;
    font-weight: bolder;
    content: "×";
}

.react-datepicker__day--keyboard-selected {
    background-color: #545776;
}

.react-datepicker__day--in-range {
    background-color: #545776;
}


.react-datepicker__tab-loop {
    display: flex;
    justify-content: center;
}


.grider-container{    
    margin-top: 2em;
    padding: 2 em;
    color: #545776;
    padding: 0 7em 0 7em;
    flex-direction: column;
    width: 100%;
}


.grider-card{
    margin-top: 2em;
    display: flex;
    justify-content: center;
    margin-right: 2rem;
}

.grider-card-item{
    display: flex;
    flex-direction: column;
    width: 21.5rem;
    margin: 0 2.5em ;
    box-shadow: 0px 3px 10px #74757e6b;
    border-radius: 6%;
    cursor: default;
}

.grider-card-item img{
    border-radius: 6% 6% 0 0;
    cursor: pointer;
}

.grider-card-details{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: left;
    padding: 1em;
}

.listing-container{
    margin-top: 2em;
    background-color: hsla(235, 40%, 75%, 0.212);
    display:  flex;
    flex-direction: column;
    }

.listing-container ul{
    color: #545776;
    display: flex;
    flex-flow: wrap;
    justify-content: center;
    margin-bottom: 2em;
}

.listing-container h1{
    
    text-align: left;
    color: #545776;
    font-size: 1.8rem;
    font-weight: bold;
    margin: 2.5rem;
    margin-left: 3em;
    
}

.listing-card{
    background-color: #fff;
    margin: 1em 2em ;
    width: 45%;
    border-radius: 1em;
    box-shadow: 0px 3px 10px #74757e6b;
    display: inline-flex;
    
}

.listing-card p{
    font-size:  medium;
}

.icons-amenities {
font-size: xx-large !important;
display: flex;
}

.icon-img {
