    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.listing-card-button{
    color: #fff !important;
    background-color: #1DBEB4;
    border-color: transparent;
    width: 100%;
    margin-top: 1em;
    position: relative;
    padding: 10px;
    border-radius: 0.5em;
    margin: auto;
