.wrapper {
    display: flex;
    padding-inline: 3%;
    margin-top: 30px;
}

.main-block {
    width: 50%;
    border-radius: 1.5em;
    padding: 0.5ex;
}

.random{
    display: flex;
    flex-flow: wrap;
    justify-content: center;
    align-items: center;
    width: 50%;
}

.random img{
    object-fit: cover;
    width: 50%;
    position: static;
    height: 50%;
    min-height: 158px;
    border-radius: 1.3em;
    padding: 5px;
}

#modal-gallery{
    bottom: 50px;
    position: relative;
    display: flex;
    text-decoration: underline;
    font-size: 1.5em;
    border: none;
    color: white;
    background-color: transparent;
    left: 40%;
}

/*M O D A L*/

.g-container{
    
    width: 45%;
    padding: 1em;
    display: flex;
    margin: auto;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: rgb(251 255 253 / 95%);
    border-radius: 1em;
    z-index: 20;
    box-shadow: 0 0 10px rgba(0,0,0,0.5);
}

.g-container--hidden{
    display:none ;
}

.close-modal {
    position:fixed;
    display: block;
    z-index: 30;
}

.main-img {
    position: relative;
    width: 100%;
    border-radius: 1em;
}

.carousel {
    min-height:15vh;
    display: flex;
    margin-top: 10px;
    width: 96%;
    overflow-x: scroll;
    scroll-snap-type: x mandatory;
    align-items: center;
    border-radius:1em;
    justify-content: center;

    scrollbar-width: none;
    -ms-overflow-style: none;
    -ms-scroll-snap-type: x mandatory;
}

.carousel img{
    animation: scroll 40s linear infinite;
    -webkit-animation: scroll 40s linear infinite;
}

@keyframes scroll {
    0%{
        -webkit-transform: translateX(0);
        transform: translateX(0);
    }
    100%{
        -webkit-transform: translateX(-100%);
        transform: translateX(-100%);
    }
}

.carousel::-webkit-scrollbar {
    display: none;
}

.carousel img{
    filter: grayscale(50%);
    transition: cubic-bezier(10) ;
    width: 24%;
    margin-inline:0.8ex;
    border-radius: 1ex;
    scroll-snap-align: center;
    object-fit: cover;
}

.carousel img:hover{
    filter: none;
}

.arrows {
    display: flex;

    flex-direction: row;
    justify-content: center;

    height: 0px;
    top: -280px;
}

.left-aw{
    
    font-size: 3.6em;
    cursor: pointer;
    color: #1dbeb4;
    margin-right: 50px;

}

.right-aw{
    
    font-size: 3.6em;
    cursor: pointer;
    color: #1dbeb4;
    margin-left: 50px;

    
}


.modal-gallery-x {
    
    
    text-decoration: none !important;
    font-size: xx-large;
    display: flow-root;
    position: relative;
    padding-top: 50p;
    right: 50px!;
    z-index: 2;
    top: 66px;
    
    margin: auto;
}

.counter-gallery {
font-weight: 600 !important;
color: #0c5a56;
}

/* CALENDAR STYLES */

.wrapper-calendar{
        display: flex;
    padding-inline: 3%;
    margin-top: 30px;
    flex-direction: row;
    background-color: #edf0f3;
    flex-direction: column;
}

.wrapper-calendar-title{
    padding: 20px 0px 0px 0px;
    gap: 10px;
}

.react-datepicker__header {
    
    background-color: unset;
    border-bottom: unset;
    padding: 20px 0;
    
}

.react-datepicker {
	
	border: unset;
	display: flex;

	justify-content: center;
    min-height: 300px;
align-items: center;
width: 110% !important;
    left: -50px;
box-shadow: 0px 3px 3px #74757e6b;
}


.react-datepicker__day-names {
	margin-bottom: -20px;
}

.react-datepicker__day-name, .react-datepicker__day, .react-datepicker__time-name {
	
	width: 2.5rem;
	
}

.react-datepicker__day-name, .react-datepicker__day, .react-datepicker__time-name :hover {
	
	background-color: white !important;
    color: black;
	
}

.react-datepicker__navigation{
    border: unset;
    border-radius: 100px;
    color: white;
    height: 35px;
    width: 35px;
    margin-top: 125px;
    background-color: #1dbeb4;
}

.react-datepicker__year-read-view--down-arrow, .react-datepicker__month-read-view--down-arrow, .react-datepicker__month-year-read-view--down-arrow, .react-datepicker__navigation-icon::before {
    border-color:unset !important;
}

.react-datepicker-wrapper-prod{
    display: flex;
align-items: center;
justify-content: center;
margin-bottom: 30px;
}

.react-datepicker-wrapper__picker{
    width: 65%;
    display: contents;
}

.react-datepicker-card{
    width: 35%;
    display: flex;
flex-direction: column;

align-items: center;
}

.react-datepicker-card__body{
    
        width: 80%;
        height: 180px;
