@import '../../App.scss';

.signup {
    padding-top: 92px;
    @include breakpoints(sm) {
        padding-top: 86px;
    }

    .form-container {
        background-color: #f4f4f4;
        color: #fff;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        max-width: 100vw;
        width: 100%;
        min-height: calc(100vh - 150px);
        height: 100%;
        padding: 32px;

        @include breakpoints(sm) {
            padding: 25px 0px 20px 0px;
            height: calc(100vh - 80px);

        }

        form {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            width: 30%;
            height: 100%;
            padding: 2rem;

            @include breakpoints(md) {
                padding: 1rem;
                width: 70%;
            }

            @include breakpoints(sm) {
                
                width: 100%;
                height: 84%;
                padding: 0;
            }
        }

        h1 {
            color: var(--color-principal);

            @include breakpoints(sm) {
                font-size: 24px;
            }
        }

        label {
            color: var(--color-2);
            align-self: flex-start;
            font-style: normal;
            font-weight: 700;
            font-size: 14px;
            line-height: 14px;
         

            @include breakpoints(sm) {
                width: 85%;
                display: block;
                align-self: flex-end;
                font-size: 12px;
            }

        }

        .form-group {
            width: 100%;
            display: inline-block;


            @include breakpoints(sm) {
                width: 85%;
                align-self: flex-end;
                font-size: 12px;

            }
        }

        input {
            width: 100%;
            height: 3rem;
            border-radius: 5px;
            border: 1px solid #fff;
            padding: 1rem; // 0.5
            margin-top: 0.2rem;
            box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.15);

            &:focus,
            &:active {
                outline: none
            }

            @include breakpoints(sm) {
                height: 40px;
                width: 80%;
                padding: 0px;
                margin-bottom: 0px;
            }
        }



        .input-half {
            display: flex;
            width: 100%;
            justify-content: space-between;
            // clear: both;
            // padding-right: 5px;

            @include breakpoints(md) {
                display: inline-block;
                justify-content: flex-start;
            }

            @include breakpoints(sm) {
                display: inline-block;
                justify-content: flex-start;
                width: 70%;
            }

            input {
                width: 100%;

                @include breakpoints(md) {
                    width: 100%;
                }

                @include breakpoints(sm) {
                    width: 100%;
                    height: 40px;
                }
            }
        }

        .hidden {
            visibility: hidden;
        }

        .hasError {
            border: 1px solid #FF0000;
            background-color: #FFE1E1;
        }

        .error {
            display: flex;
            justify-content: flex-end;
            width: 100%;
            // visibility: hidden;

            @include breakpoints(sm) {
                justify-content: flex-start;

            }

            small {
                color: #FF0000;
                font-size: 12px;
                font-weight: 700;
                padding: 8px;

                @include breakpoints(sm) {
                    width: 100%;
                    padding-left: 0px;

                }
            }
        }

        button {
            color: var(--blanco);
            background-color: var(--color-principal);
            border: 1px solid var(--color-principal);
            padding: 10px;
            font-family: 'Roboto', cursive;
            font-size: 16px;
            font-weight: 600;
            margin: 10px;
            width: 200px;
            border-radius: 5px;
            box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.12);
            cursor: pointer;

            @include breakpoints(md) {
                width: 100%;
            }

            @include breakpoints(sm) {
                width: 70%;
            }
        }

        span {
            color: var(--color-2);
            font-style: normal;
            font-weight: 700;
            font-size: 14px;

            @include breakpoints(sm) {
                width: 70%;
            }
        }
    }
}